@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;600;700&display=swap');

:root {
    --color-light: #f5f5f5;
    --color-primary: rgba(12, 189, 183, 0.7);
    --color-primary-variant: rgba(12, 189, 183, 0.3);

    --color-bg: #0a1828;
    --color-bg-light: #0e1a2b;
    --color-red: #ff595a;

    --color-white: rgb(250, 249, 246, 0.9);
    --color-light: rgba(225, 255, 225, 0.6);

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 92%;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Source Sans Pro', sans-serif;
    background: var(--color-bg);
    color: var(--color-light);
    line-height: 1.7;
    background-image: url(../src/utils/imgs/bg-texture.jpg);
}

.container {
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 500;
}

h1 {
    font-size: 3rem;
}

section {
    margin-top: 8rem;
}

section > h2,
section > h5 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--color-light);
}

a {
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover {
    color: var(--color-white);
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border: 1px solid var(--color-primary);
    border-radius: 0.5rem;
    transition: var(--transition);
    cursor: pointer;
}

.btn:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: var(--color-primary);
    color: var(--color-bg);
}

img {
    width: 100%;
    height: auto;
    display: block;
    object-fit: cover;
}

/*==================MEDIA QUERIES (MEDIUM DEVICES)===================*/

@media screen and (max-width: 1024px) {
    .container {
        width: var(--container-width-md);
    }

    section {
        margin-top: 6rem;
    }
}

/*==================MEDIA QUERIES (SMALL DEVICES)===================*/

@media screen and (max-width: 768px) {
    .container {
        width: var(--container-width-sm);
    }

    section {
        margin: 4rem 0 1rem 0;
    }

    section > h2 {
        margin-bottom: 2rem;
    }
}
