header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

.me_img {
    width: 100%;
    height: 180%;
    object-fit: cover;
}

.name {
    margin-top: 1rem;
    font-size: 3rem;
    color: var(--color-primary);
}

@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }
}

/*==================MEDIA QUERIES (SMALL DEVICES)===================*/

@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }
}
